import "core-js/modules/es.array.iterator.js";
import "core-js/modules/web.dom-collections.iterator.js";
import "core-js/modules/web.dom-collections.for-each.js";
import "core-js/modules/esnext.global-this.js";
// components
import Nav from "Shared/ts/components/nav";
import Carousel from "shared/ts/components/carousel";
import ResponsiveCarousel from "Shared/ts/components/responsive-carousel";
import Accordion from "Shared/ts/components/accordion";
import Toast from "shared/ts/components/toast";
import FadeCarousel from "Shared/ts/components/fade-carousel"; // adapters

import SlideCarouselAdapter from "Shared/ts/api/carousel/slide/adapters/slide-carousel";
import ResponsiveSlideCarouselAdapter from "Shared/ts/api/carousel/slide/adapters/responsive-slide-carousel";
import FadeSlideCarouselAdapter from "Shared/ts/api/carousel/slide/adapters/fade-slide-carousel"; // utils

import LoadItem from "Shared/ts/utils/load-item";
import { createElement, elementExists, enumerateElements } from "shared/ts/utils/html"; // observers

import MediaQuery from "Shared/ts/observers/media-query";
import { observer } from "shared/ts/observers/intersection";
import VimeoManager from "Shared/ts/utils/vimeo-manager"; // nav

var nav = new Nav("nav");

var fixateNav = () => nav.root.classList.add("nav--is-fixed");

var undoFixateNav = () => nav.root.classList.remove("nav--is-fixed");

observer("[data-nav-fixate]", {
  inRange: () => undoFixateNav(),
  outRange: () => fixateNav(),
  unObserve: false
}); // defer images

observer("[data-src-img]", {
  inRange: record => new LoadItem(record)
}); // defer vimeo player iframes

var vimeoManager = new VimeoManager();
observer("[data-src-iframe]", {
  inRange: record => {
    new LoadItem(record, {
      tag: "iframe",
      src: "data-src-iframe"
    });
    var vimeoUrl = record.getAttribute("data-src-iframe");
    var vimeoId = vimeoManager.getIdByUrl(vimeoUrl);
    vimeoManager.generatePosterImage(vimeoId).then(poster => {
      var image = createElement("img", {
        src: poster
      });
      record.insertAdjacentElement("afterbegin", image);
    });
  }
});

var handleCarouselImages = (carousel, record) => {
  var slides = record.querySelectorAll(".slide__item");
  var map = new WeakMap();
  carousel.on("rotation", currentIndex => {
    var currentSlide = slides.item(currentIndex);

    if (!map.has(currentSlide)) {
      var placeholderImages = enumerateElements(currentSlide.querySelectorAll("[data-src-img]"));
      placeholderImages.forEach(placeholderImage => new LoadItem(placeholderImage));
      map.set(currentSlide, placeholderImages);
    }
  });
}; // before and after


observer(".slide--before-after", {
  inRange: record => {
    var carousel = new ResponsiveCarousel(new ResponsiveSlideCarouselAdapter(record));
    carousel.enablePrevNextControls();
    new MediaQuery("(min-width: 0px) and (max-width: 499px)").inbound(() => carousel.setAttributes({
      steps: 1
    }));
    new MediaQuery("(min-width: 500px) and (max-width: 999px)").inbound(() => carousel.setAttributes({
      steps: 2
    }));
    new MediaQuery("(min-width: 1000px)").inbound(() => carousel.setAttributes({
      steps: 3
    }));
    handleCarouselImages(carousel, record);
  }
}); // influencer

observer(".slide--influencer", {
  inRange: record => {
    var carousel = new ResponsiveCarousel(new ResponsiveSlideCarouselAdapter(record));
    carousel.enablePrevNextControls();
    new MediaQuery("(min-width: 0px) and (max-width: 399px)").inbound(() => carousel.setAttributes({
      steps: 1
    }));
    new MediaQuery("(min-width: 400px) and (max-width: 749px)").inbound(() => carousel.setAttributes({
      steps: 2
    }));
    new MediaQuery("(min-width: 750px) and (max-width: 999px)").inbound(() => carousel.setAttributes({
      steps: 3
    }));
    new MediaQuery("(min-width: 1000px)").inbound(() => carousel.setAttributes({
      steps: 4
    }));
    handleCarouselImages(carousel, record);
  }
}); // testimonials carousel

observer(".slide--testimonial", {
  inRange: record => {
    var carousel = new Carousel(new SlideCarouselAdapter(record));
    carousel.enablePrevNextControls();
  }
}); // accordion

var createAccordion = element => {
  if (elementExists(element)) {
    new Accordion(element);
  }
};

enumerateElements(document.querySelectorAll(".accordion")).forEach(accordion => {
  createAccordion(accordion);
}); // toast

var toast = new Toast("toast-status");
globalThis.toast = toast; // form dropdowns

var imageDropdowns = enumerateElements(document.querySelectorAll(".bundle__image-dropdown"));
imageDropdowns.forEach(imageDropdown => {
  var image = imageDropdown.querySelector(".slide"),
      dropdown = imageDropdown.querySelector("select");

  if (elementExists(image) && elementExists(dropdown)) {
    var carousel = new FadeCarousel(new FadeSlideCarouselAdapter(image));
    setTimeout(() => {
      carousel.goto(dropdown.selectedIndex);
    }, 100);
    dropdown.addEventListener("change", () => {
      carousel.goto(dropdown.selectedIndex);
    });
  }
});